import React from 'react';
import todos from '../api/todos';
import { TodoList } from '../displayTodo/TodoList';
import users from '../api/users';
import Form from './Form';

const preparedTodos = todos.map(todo => ({
  ...todo,
  user: users.find(person => person.id === Number(todo.userId)),
}));

export class NewTodo extends React.Component {
  state = {
    userId: 0,
    title: '',
    lastId: 5,
    id: 0,
  }

  selectUser = (event) => {
    this.setState({
      userId: Number(event.target.value),
    });
  }

  renameTodo = (title) => {
    this.setState({ title });
  }

  letId = (event) => {
    this.setState(past => ({
      id: past.lastId,
      lastId: Number(past.lastId) + 1,
    }));

    setTimeout(() => {
      this.saveTodos();
    }, 0);
  }

  saveTodos = () => {
    this.setState((prev) => {
      preparedTodos[prev.id] = {
        userId: prev.userId,
        id: prev.id,
        title: prev.title,
        completed: false,
        user: users.find(person => person.id === prev.userId),
      };
    });
  }

  render() {
    return (
      <>
        <Form
          selectUser={event => this.selectUser(event)}
          users={this.props.users}
          title={this.props.title}
          renameTodo={() => this.renameTodo}
          letId={event => this.letId(event)}
        />
        <TodoList props={preparedTodos} />
      </>
    );
  }
}
