import React from 'react';
import { AddUser } from './addElements/AddUser';
import { AddTitle } from './addElements/AddTitle';

const Form = (props) => {
  const { selectUser, users, title, renameTodo, letId } = props;

  return (
    <form>
      <h1>Add Todo:</h1>

      <select onChange={selectUser} required>
        <option />
        {users.map(user => (
          <React.Fragment key={user.id}>
            <AddUser {...user} />
          </React.Fragment>
        ))}
      </select>

      <AddTitle
        title={title}
        rename={renameTodo()}
      />

      <button
        type="button"
        onClick={letId}
      >
        Save
      </button>
    </form>
  )
}

export default Form;
