import React from 'react';
import PropTypes from 'prop-types';

export const AddTitle = ({ title, rename }) => {

  return (
    <input
      required
      value={title}
      maxLength={100}
      type="text"
      onChange={event => rename(event.target.value)}
    />
  );
};

AddTitle.propTypes = {
  title: PropTypes.string.isRequired,
  rename: PropTypes.func.isRequired,
};
